import apiClient from './client';
import type { Post } from '@/types/post';

export interface SavedPostsResponse {
  posts: (Post & { savedAt: string })[];
  pagination: {
    page: number;
    limit: number;
    totalCount: number;
    totalPages: number;
    hasMore: boolean;
  };
}

export interface ToggleSaveResponse {
  message: string;
  saved: boolean;
  savesCount: number;
}

// Get all saved posts
export const getSavedPosts = async (page = 1, limit = 10): Promise<SavedPostsResponse> => {
  const response = await apiClient.get('/saved', {
    params: { page, limit },
  });
  return response.data;
};

// Toggle save status
export const toggleSavePost = async (postId: string): Promise<ToggleSaveResponse> => {
  const response = await apiClient.post(`/saved/${postId}/toggle`);
  return response.data;
};

// Save a post
export const savePost = async (postId: string): Promise<{ message: string }> => {
  const response = await apiClient.post(`/saved/${postId}`);
  return response.data;
};

// Unsave a post
export const unsavePost = async (postId: string): Promise<{ message: string }> => {
  const response = await apiClient.delete(`/saved/${postId}`);
  return response.data;
};

// Check if post is saved
export const checkPostSaved = async (postId: string): Promise<{ saved: boolean }> => {
  const response = await apiClient.get(`/saved/${postId}/check`);
  return response.data;
};
